.header {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  height: 64px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 32px;
  background: transparent;
  transition: all ease 0.3s;
}
.header--black {
  background-color: #141414;
}

.header__logo {
  height: 24px;
}

.header__user {
  height: 32px;
  border-radius: 4px;
}

